.App {
  text-align: center;
  background: radial-gradient(73.79% 50% at 50% 50%, #8C27B0 0%, rgba(108, 40, 132, 0) 64.58%);
  background-color: #0E0E23;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.CButton {
  background: #0E0E23;
  color: white;
  font-size: 30px;
  border: none;
  padding: 15px;
  border-radius: 15px;
}

@media only screen and (min-width:400px) {
  .CButton {
    background: #0E0E23;
    color: white;
    font-size: 40px;
    border: none;
    padding: 15px;
    border-radius: 15px;
  }
}

.CButton:hover {
  cursor: pointer;
  background-color: white;
  color: #8001DB;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: SpaceMonoItalic;
  font-weight: 2;
  src: url(./fonts/SpaceMono-Italic.ttf);
}

@font-face {
  font-family: SpaceMonoBold;
  font-weight: 2;
  src: url(./fonts/SpaceMono-Bold.ttf);
}

.text-spaceItalic {
  font-family: SpaceMonoItalic;
}

.text-spaceBold {
  font-family: SpaceMonoBold;
}

.stateCss {
  color: white;
  font-size: 30px;
}

.modal {
  padding: 5px;
  display: flex;
  width: 60%;
  background-color: rgb(247, 246, 246);
  border-radius: 25px;
  border-width: 2;
}

@media only screen and (min-width:700px) {
  .modal {
    padding: 5px;
    display: flex;
    width: 50%;
    background-color: rgb(247, 246, 246);
    border-radius: 5px;
  }
}

.connectbox {
  width: 50%;
  border-color: white;
  border-width: 2px;
  display: flex;
  color: black;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.connectbox:hover {
  cursor: pointer;
  background-color: rgb(11, 21, 49);
  color: #838383;
  opacity: 1;
}

.titletext {
  margin: 0px;
  font-size: 15px;
}

.contenttext {
  margin: 1px;
  font-size: 10px;
}

@media only screen and (min-width:600px) {
  .titletext {
    margin: 0px;
    font-size: 25px;
  }

  .contenttext {
    margin: 1px;
    font-size: 15px;
  }
}

.logo {
  width: 30%;
  border-radius: 30px;
}